<template>
    <div class="AccountSetting">
      <div class="HomePageTop bg_fff">
        <div class="shade"></div>
        <div class="Head">
          <i class="iconfont pa left24" @click="prev"></i>
          子账号管理
         <p class="pa right24 top0" @click="show=true">添加</p>
      </div>
      </div>
      <div>
        <van-collapse v-model="activeNames" class="vantcollapse" accordion>
          <van-collapse-item title="编号" :name=index  :value=data.text class="mt24" v-for="(data,index) in TallageData" :key="index" >
            <template #title>
              <div @click.stop="SelectHe(data,index)"> <i class="icons" :class="SelsetIndex==index?'bg_icons':'bg_icon'"></i> <span class="vm f34">客编号</span>

              <span class="primary">{{data.minute}}</span>
              </div>
            </template>
            <div>
              <div class="specific">
                <p>
                  <span>公司名称：</span>
                  <span>{{data.leixing}}</span>
                </p>
                <p>
                  <span>代付公司名称：</span>
                  <span>{{data.fangshi}}</span>
                </p>
              </div>
              <div class="remove" @click="AccountSettingCompile(data,index)">
                <p>编辑代付公司</p>
              </div>
            </div>
          </van-collapse-item>
        </van-collapse>
      </div>
<div @click="objs(data,i)"></div>



      <van-dialog v-model="show" title="添加子账号" show-cancel-button width="5.4rem" confirmButtonText="确定" confirmButtonColor="#f90" @confirm="increase">
        <div class="AdditionPall">
          <input type="text" placeholder="请输入子账号公司名称" @input="" v-model="InputText">
          <textarea name="" id="" cols="30" rows="10"  placeholder="请输入子账号备注"  v-model="TextareaTexe"></textarea>

        </div>
      </van-dialog>

    </div>

</template>

<script>
    import Vue from "vue"

    export default {
        name: "AccountSetting",
        components: {},
        data() {
            return {
              activeNames: ['1'],
              TallageData:[
                {name:true,text:"J17733A",leixing:"捷配",fangshi:"捷配协同工厂",select:true,minute:"主"},
                {name:true,text:"J17733A",leixing:"捷配",fangshi:"捷配协同工厂",select:true,minute:"子"},
                {name:true,text:"J17733A",leixing:"捷配",fangshi:"捷配协同工厂",select:true,minute:"子"},
                {name:true,text:"J17733A",leixing:"捷配",fangshi:"捷配协同工厂",select:true,minute:"子"},
              ],
              SelsetIndex:"",
              show: false,
              //双向数据绑定弹出层
              InputText:"",
              TextareaTexe:"",
            }
        },
        methods: {
          //跳转到编辑代付公司
          AccountSettingCompile(data,index){
            this.$router.push({
              name:"AccountSettingCompile",
              params:{data:data,index:index}
            })
          },
          SelectHe(data,index){
            this.SelsetIndex=index
          },
          //增加子账号
          increase(){
            if(this.InputText!=""&&this.TextareaTexe!=""){
              let objs={name:true,text:"J17733A",leixing:this.InputText,fangshi:this.TextareaTexe,select:true,minute:"子"}
              this.TallageData.push(objs);
              this.show=false;
              this.$store.commit("TallageData",this.TallageData);
              this.$router.push({
                path:"AccountSettingSucceed"
              })
            }

          },
          prev(){
            this.$router.go(-1)
          },
        },
        created() {
          if(this.$store.state.vuex.TallageDataHome.length==0){

          }else{
            this.TallageData=this.$store.state.vuex.TallageDataHome
          }
          //获取编辑代付公司传递过来的数据
          if(this.$route.params.index !=undefined){
            this.TallageData[this.$route.params.index].fangshi=this.$route.params.name
          }

        }
    }

</script>

<style lang="less">

  ::-webkit-input-placeholder {
    /* WebKit, Blink, Edge */
    color: #CCCCCC;
  }

  /*:-moz-placeholder {*/
  /*  !* Mozilla Firefox 4 to 18 *!*/
  /*  color: #CCCCCC;*/
  /*}*/

  /*::-moz-placeholder {*/
  /*  !* Mozilla Firefox 19+ *!*/
  /*  color: #CCCCCC;*/
  /*}*/

  /*:-ms-input-placeholder {*/
  /*  !* Internet Explorer 10-11 *!*/
  /*  color: #CCCCCC;*/
  /*}*/
    .van-dialog{
    border-radius: 0;
      padding-top: 0;

  }
  .AdditionPall{
    padding: .4rem .3rem .3rem .3rem;
    text-align: center;
    font-size: .28rem;
    color: #333;

    input{
      border: 1px solid #D1D3D8;
      width: 4.8rem;
      border-radius: .04rem;
      /*height: .72rem;*/
      line-height: .72rem;
      padding-left: .2rem;
      text-align: left;
    }
    textarea{
      margin-top: .24rem;
      border: 1px solid #D1D3D8;
      width: 4.8rem;
      height: 1.44rem;
      padding: .2rem;
      resize:none;
    }
  }
  .icons{  width: .4rem;   height: .4rem;   display: inline-block;    vertical-align: middle; }
  .bg_icon{  background: url("../../../assets/img/common/Nopitch.png") no-repeat;  background-size: cover; }
  .bg_icons{  background: url("../../../assets/img/common/pitch.png") no-repeat; background-size: cover;}
  .primary{  width: .68rem;  height: .44rem;  display: inline-block; vertical-align: middle; background: #FFF5E5; color: #f90; line-height: .44rem;text-align: center;font-size: .28rem;}
  .specific p{ margin-bottom: .20rem;display:flex;justify-content: space-between;}
  .remove{ font-size: .28rem;text-align: right;height: 1.02rem;line-height:1.02rem;}
  .remove p{display: inline-block;text-align: center;height: .62rem;line-height: .62rem;border: 1px solid #f90;width: 2.05rem;color: #ff9900;}

</style>



